<template>
  <v-toolbar color="black" class="white--text">
    <v-toolbar-side-icon @click.stop="drawer = !drawer" class="hidden-md-and-up"></v-toolbar-side-icon>
    <router-link to="/" style="text-decoration: none; color: #fff"><v-btn flat><v-toolbar-title>
    <v-avatar
          size="45"
          color="grey lighten-4"
        >
          <img src="http://logok.org/wp-content/uploads/2015/05/Marvel-logo-880x660.png" alt="avatar">
        </v-avatar>
    </v-toolbar-title></v-btn></router-link>
    <v-spacer></v-spacer>

    <div class="hidden-sm-and-down">
      <router-link to="login" style="text-decoration: none; color: #fff"><v-btn flat>Login</v-btn></router-link>
      <router-link to="contact" style="text-decoration: none; color: #fff" ><v-btn flat>Contact</v-btn></router-link>
    </div>

    <div class="hidden-md-and-up">
	    <v-layout
	    wrap
	    style="height: 400px;"
	  	>
    

	    <v-navigation-drawer
	      v-model="drawer"
	      absolute
	      temporary
	      style="background: #000; height: 100vh;"
	    >
	      <v-list class="pa-1">
	        <v-list-tile avatar>
	          <v-list-tile-avatar>
	            <img src="https://randomuser.me/api/portraits/men/85.jpg" >
	          </v-list-tile-avatar>

	          <v-list-tile-content>
	            <v-list-tile-title>Your name</v-list-tile-title>
	          </v-list-tile-content>
	        </v-list-tile>
	      </v-list>

	      <v-list class="pt-0" dense>
	        <v-divider></v-divider>

	        <v-list-tile
	          v-for="item in items"
	          :key="item.title"
	        >
	          <v-list-tile-action>
	            <v-icon>{{ item.icon }}</v-icon>
	          </v-list-tile-action>

	          <v-list-tile-content>
	            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
	          </v-list-tile-content>
	        </v-list-tile>
	      </v-list>
	    </v-navigation-drawer>
	  </v-layout>
  </div>

  </v-toolbar>
</template>

<script>
  export default {
    data () {
      return {
        drawer: null,
        items: [
          { title: 'Home', icon: 'dashboard' },
          { title: 'About', icon: 'question_answer' }
        ],
        mini: false,
        right: null
      }
    }
  }
</script>

<style scoped>
</style>


