<template>
  <v-carousel>
    <v-carousel-item
      v-for="(item,i) in items"
      :key="i"
      :src="item.src"
    ></v-carousel-item>
  </v-carousel>
</template>
<script>
  export default {
    data () {
      return {
        items: [
          {
            src: 'https://i.pinimg.com/originals/a8/26/eb/a826eb5c045f94b5644e6372994d4b79.jpg'
          },
          {
            src: 'https://i.imgur.com/8enncLP.jpg'
          },
          {
            src: 'https://stmed.net/sites/default/files/avengers-wallpapers-27506-3899878.jpg'
          },
          {
            src: 'https://i.imgur.com/NqyZ3ue.jpg'
          }
        ]
      }
    }
  }
</script>
<style scoped>
.v-jumbotron__image {
	width: 100%;
	height: 100vh;
	background-position: center;
	background-size: cover;
}	
</style>


