<template>
  <v-layout mt-3>
    <v-flex xs12 sm7 offset-sm3 >
      <v-card>
        <v-card-media
          src="http://longwallpapers.com/Desktop-Wallpaper/marvel-wallpaper-widescreen-For-Desktop-Wallpaper.jpg"
          height="200px"
        ></v-card-media>

        <v-card-text primary-title>
          <div>
            <h3 class="headline mb-0">Marvel Studios</h3>
            <div>Marvel Entertainment, LLC 135 W. 50th Street New York, NY 10020</div><br>
            <div>{{ card_text }}</div>
          </div>
        </v-card-text>

      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
  export default {
    data () {
      return {
        card_text: 'Marvel Studios, LLC (originally known as Marvel Films from 1993 to 1996) is an American motion picture studio based at the Walt Disney Studios in Burbank, California and is a subsidiary of Walt Disney Studios, itself a wholly owned division of The Walt Disney Company, with film producer Kevin Feige serving as president. Previously, the studio was a subsidiary of Marvel Entertainment until Disney reorganized the companies in August 2015'
      }
    }
  }
</script>


