<template>
  <v-container>

    <v-alert
      :value="alert"
      type="warning"
      color="red"
    >
      Check again the fields.
    </v-alert>
    <v-alert
      :value="success"
      type="success"
      color="lime"
    >
      You are awensome.
    </v-alert>

    <v-form v-model="valid" v-on:submit.prevent="submit">
      <v-text-field
        v-model="email"
        :rules="emailRules"
        label="E-mail"
        required
      ></v-text-field>
      <v-text-field
        v-model="password"
        label="Password"
        type="password"
        required
      ></v-text-field>
      <v-btn class="info black--text" type="submit">Submit</v-btn>
    </v-form>

    

  </v-container>
</template>

<script>
  export default {
    data(){
      return {
        valid: false,
        alert: false,
        success: false,
        email: '',
        password: '',
        emailRules: [
          v => !!v || 'E-mail is required',
          v => /.+@.+/.test(v) || 'E-mail must be valid'
        ]
      } 
    },
    methods: {
      submit(){
        if(!(this.email && this.password)){
          this.alert = true;
        }else{
          this.alert = false;
          this.success = true;
        }
      }
    }
  }
</script>


